"use client"
import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Plus } from "lucide-react"
import { PaquetesTable } from "./paquetes-table"
import { PaqueteModal } from "./paquete-modal"

export function PaquetesModule() {
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [paquetes, setPaquetes] = useState([
    {
      id: 1,
      nombre: "Caribbean Paradise",
      destino: "Bahamas",
      fechasDisponibles: "01/12/2024 - 30/01/2025",
      cupos: 8,
      precioAdulto: 2500,
      precioMenor: 1800,
      moneda: "USD",
      tarifa: "Alta",
      servicios: "Vuelo, Alojamiento 5*, All Inclusive, Tours",
      politicas: "Cancelación gratuita 30 días antes",
      imagen: "/playa-caribe-a.jpg",
      notas: "Incluye excursion a Atlantis",
    },
    {
      id: 2,
      nombre: "European Tour",
      destino: "Europa",
      fechasDisponibles: "15/04/2025 - 30/05/2025",
      cupos: 12,
      precioAdulto: 3200,
      precioMenor: 2400,
      moneda: "EUR",
      tarifa: "Media",
      servicios: "Vuelo, Hotel 4*, Autobús turístico, Guía",
      politicas: "50% anticipo, resto 15 días antes",
      imagen: "/europa-ciudades.jpg",
      notas: "Paris, Londres, Ámsterdam, Berlín",
    },
  ])
  const [editingId, setEditingId] = useState<number | null>(null)

  const handleSubmit = (data: any) => {
    if (editingId) {
      setPaquetes(paquetes.map((p) => (p.id === editingId ? { ...data, id: editingId } : p)))
      setEditingId(null)
    } else {
      setPaquetes([...paquetes, { ...data, id: Date.now() }])
    }
    setIsModalOpen(false)
  }

  const handleEdit = (paquete: any) => {
    setEditingId(paquete.id)
    setIsModalOpen(true)
  }

  const handleDelete = (id: number) => {
    setPaquetes(paquetes.filter((p) => p.id !== id))
  }

  return (
    <div className="w-full h-full flex flex-col gap-4 p-4 md:p-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4">
        <div>
          <h1 className="text-2xl md:text-3xl font-bold text-foreground">Paquetes Turísticos</h1>
          <p className="text-muted-foreground mt-1">Gestiona todos tus paquetes y ofertas</p>
        </div>
        <Button
          onClick={() => {
            setEditingId(null)
            setIsModalOpen(true)
          }}
          className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white flex items-center gap-2 w-full sm:w-auto"
        >
          <Plus className="w-4 h-4" />
          Nuevo Paquete
        </Button>
      </div>

      {/* Table */}
      <PaquetesTable paquetes={paquetes} onEdit={handleEdit} onDelete={handleDelete} />

      {/* Modal */}
      <PaqueteModal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false)
          setEditingId(null)
        }}
        onSubmit={handleSubmit}
        editingPaquete={editingId ? paquetes.find((p) => p.id === editingId) : null}
      />
    </div>
  )
}
