"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { VendedoresTable } from "./vendedores-table"
import { VendedorModal } from "./vendedor-modal"
import { Plus, Search } from "lucide-react"

export function VendedoresModule() {
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingVendedor, setEditingVendedor] = useState<any>(null)
  const [searchTerm, setSearchTerm] = useState("")
  const [vendedores, setVendedores] = useState([
    {
      id: "1",
      nombre: "Carlos",
      apellido: "González",
      email: "carlos@moana.com",
      whatsapp: "+34612345678",
      sucursal: "Buenos Aires",
    },
    {
      id: "2",
      nombre: "María",
      apellido: "López",
      email: "maria@moana.com",
      whatsapp: "+34623456789",
      sucursal: "Córdoba",
    },
    {
      id: "3",
      nombre: "Juan",
      apellido: "Rodríguez",
      email: "juan@moana.com",
      whatsapp: "+34634567890",
      sucursal: "Rosario",
    },
  ])

  const handleAddVendedor = (data: any) => {
    if (editingVendedor) {
      setVendedores(vendedores.map((v) => (v.id === editingVendedor.id ? { ...v, ...data } : v)))
      setEditingVendedor(null)
    } else {
      const newVendedor = {
        id: String(Date.now()),
        ...data,
      }
      setVendedores([...vendedores, newVendedor])
    }
    setIsModalOpen(false)
  }

  const handleDeleteVendedor = (id: string) => {
    if (confirm("¿Estás seguro de que deseas eliminar este vendedor?")) {
      setVendedores(vendedores.filter((v) => v.id !== id))
    }
  }

  const handleEditVendedor = (vendedor: any) => {
    setEditingVendedor(vendedor)
    setIsModalOpen(true)
  }

  const handleCloseModal = () => {
    setIsModalOpen(false)
    setEditingVendedor(null)
  }

  const filteredVendedores = vendedores.filter(
    (v) =>
      v.nombre.toLowerCase().includes(searchTerm.toLowerCase()) ||
      v.apellido.toLowerCase().includes(searchTerm.toLowerCase()) ||
      v.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      v.sucursal.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  return (
    <div className="p-6 space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-foreground">Vendedores</h1>
        <p className="text-muted-foreground mt-1">Gestiona tu equipo de ventas</p>
      </div>

      <Card className="p-6">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 mb-6">
          <div className="relative w-full md:w-64">
            <Search className="absolute left-3 top-3 w-4 h-4 text-muted-foreground" />
            <input
              type="text"
              placeholder="Buscar vendedor..."
              className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-card text-foreground placeholder-muted-foreground focus:outline-none focus:ring-2 focus:ring-blue-500"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          <Button
            onClick={() => {
              setEditingVendedor(null)
              setIsModalOpen(true)
            }}
            className="bg-blue-600 hover:bg-blue-700 text-white gap-2 w-full md:w-auto"
          >
            <Plus className="w-4 h-4" />
            Nuevo Vendedor
          </Button>
        </div>

        <VendedoresTable vendedores={filteredVendedores} onEdit={handleEditVendedor} onDelete={handleDeleteVendedor} />
      </Card>

      <VendedorModal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        onSubmit={handleAddVendedor}
        vendedor={editingVendedor}
      />
    </div>
  )
}
