"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { LeadsTable } from "./leads-table"
import { LeadModal } from "./lead-modal"
import { LeadHistory } from "./lead-history"
import { ImportLeadsModal } from "./import-leads-modal"
import { AssignmentRulesModal } from "./assignment-rules-modal"
import { Plus, Search, Upload, Settings } from "lucide-react"

interface Lead {
  id: string
  nombre: string
  apellido: string
  email: string
  telefono: string
  pais?: string
  ciudad?: string
  tipoConsulta: string
  origen: string
  vendedorAsignado: string
  estado: string
  notasInternas?: string
  paqueteSugerido?: string
  fechaIngreso: string
}

export function LeadsModule() {
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isImportOpen, setIsImportOpen] = useState(false)
  const [isRulesOpen, setIsRulesOpen] = useState(false)
  const [selectedLeadHistory, setSelectedLeadHistory] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState("")

  const [paquetesDisponibles] = useState([
    {
      id: 1,
      nombre: "Caribbean Paradise",
      destino: "Bahamas",
      fechasDisponibles: "01/12/2024 - 30/01/2025",
      cupos: 8,
      precioAdulto: 2500,
      precioMenor: 1800,
      moneda: "USD",
      tarifa: "Alta",
      servicios: "Vuelo, Alojamiento 5*, All Inclusive, Tours",
      politicas: "Cancelación gratuita 30 días antes",
      imagen: "/playa-caribe-a.jpg",
      notas: "Incluye excursion a Atlantis",
    },
    {
      id: 2,
      nombre: "European Tour",
      destino: "Europa",
      fechasDisponibles: "15/04/2025 - 30/05/2025",
      cupos: 12,
      precioAdulto: 3200,
      precioMenor: 2400,
      moneda: "EUR",
      tarifa: "Media",
      servicios: "Vuelo, Hotel 4*, Autobús turístico, Guía",
      politicas: "50% anticipo, resto 15 días antes",
      imagen: "/europa-ciudades.jpg",
      notas: "Paris, Londres, Ámsterdam, Berlín",
    },
    {
      id: 3,
      nombre: "Asia Explorer",
      destino: "Asia",
      fechasDisponibles: "20/03/2025 - 15/04/2025",
      cupos: 10,
      precioAdulto: 2800,
      precioMenor: 2100,
      moneda: "USD",
      tarifa: "Media",
      servicios: "Vuelo, Hotel 4*, Tours guiados, Comidas",
      politicas: "Cancelación 45 días antes",
      imagen: "/asia-viaje.jpg",
      notas: "Tailandia, Vietnam, Camboya",
    },
  ])

  const [leads, setLeads] = useState<Lead[]>([
    {
      id: "1",
      nombre: "Juan",
      apellido: "García",
      email: "juan@example.com",
      telefono: "+34 612 345 678",
      pais: "España",
      ciudad: "Barcelona",
      tipoConsulta: "Paquete turístico",
      origen: "Web",
      vendedorAsignado: "Carlos González",
      estado: "Nuevo",
      notasInternas: "Interesado en viajes de lujo",
      paqueteSugerido: "Caribbean Paradise",
      fechaIngreso: "2025-12-08",
    },
    {
      id: "2",
      nombre: "María",
      apellido: "López",
      email: "maria@example.com",
      telefono: "+34 698 765 432",
      pais: "España",
      ciudad: "Madrid",
      tipoConsulta: "Hotel",
      origen: "Instagram",
      vendedorAsignado: "María López",
      estado: "En gestión",
      notasInternas: "Viaje en familia, 4 personas",
      paqueteSugerido: "Beach Resort",
      fechaIngreso: "2025-12-06",
    },
    {
      id: "3",
      nombre: "Carlos",
      apellido: "Rodríguez",
      email: "carlos@example.com",
      telefono: "+34 645 123 789",
      pais: "España",
      ciudad: "Valencia",
      tipoConsulta: "Vuelo",
      origen: "WhatsApp",
      vendedorAsignado: "Juan Pérez",
      estado: "Cotizado",
      notasInternas: "Presupuesto enviado",
      paqueteSugerido: "European Tour",
      fechaIngreso: "2025-12-03",
    },
  ])

  const [leadHistory, setLeadHistory] = useState<Record<string, any[]>>({
    "1": [
      { fecha: "2025-12-08 10:30", accion: "Lead creado desde Web", usuario: "Sistema" },
      { fecha: "2025-12-08 14:22", accion: "Email de bienvenida enviado", usuario: "Sistema" },
    ],
    "2": [
      { fecha: "2025-12-06 11:45", accion: "Lead creado desde Instagram", usuario: "Sistema" },
      { fecha: "2025-12-07 16:30", accion: "Asignado a María López", usuario: "Administrador" },
    ],
    "3": [
      { fecha: "2025-12-03 08:00", accion: "Lead creado desde WhatsApp", usuario: "Sistema" },
      { fecha: "2025-12-04 13:20", accion: "Presupuesto enviado", usuario: "Juan Pérez" },
    ],
  })

  const handleAddLead = (data: any) => {
    const newLead: Lead = {
      id: String(leads.length + 1),
      ...data,
      fechaIngreso: new Date().toISOString().split("T")[0],
    }
    setLeads([...leads, newLead])
    setLeadHistory({
      ...leadHistory,
      [newLead.id]: [{ fecha: new Date().toLocaleString(), accion: "Lead creado manualmente", usuario: "Usuario" }],
    })
    setIsModalOpen(false)
  }

  const handleImportLeads = (importedLeads: any[]) => {
    const newLeads = importedLeads.map((lead, idx) => ({
      id: String(leads.length + idx + 1),
      ...lead,
      fechaIngreso: new Date().toISOString().split("T")[0],
    }))
    setLeads([...leads, ...newLeads])
    newLeads.forEach((lead) => {
      setLeadHistory({
        ...leadHistory,
        [lead.id]: [{ fecha: new Date().toLocaleString(), accion: "Importado desde Excel", usuario: "Sistema" }],
      })
    })
    setIsImportOpen(false)
  }

  const handleDeleteLead = (id: string) => {
    setLeads(leads.filter((l) => l.id !== id))
    const newHistory = { ...leadHistory }
    delete newHistory[id]
    setLeadHistory(newHistory)
  }

  const handleUpdateLead = (id: string, updates: any) => {
    setLeads(leads.map((l) => (l.id === id ? { ...l, ...updates } : l)))
    setLeadHistory({
      ...leadHistory,
      [id]: [
        ...(leadHistory[id] || []),
        { fecha: new Date().toLocaleString(), accion: "Lead actualizado", usuario: "Usuario" },
      ],
    })
  }

  const filteredLeads = leads.filter(
    (l) =>
      `${l.nombre} ${l.apellido}`.toLowerCase().includes(searchTerm.toLowerCase()) ||
      l.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      l.telefono.includes(searchTerm),
  )

  const getEstadoColor = (estado: string) => {
    switch (estado) {
      case "Nuevo":
        return "bg-blue-100 dark:bg-blue-900/30 text-blue-700 dark:text-blue-300"
      case "En gestión":
        return "bg-amber-100 dark:bg-amber-900/30 text-amber-700 dark:text-amber-300"
      case "Cotizado":
        return "bg-purple-100 dark:bg-purple-900/30 text-purple-700 dark:text-purple-300"
      case "Cerrado ganado":
        return "bg-emerald-100 dark:bg-emerald-900/30 text-emerald-700 dark:text-emerald-300"
      case "Cerrado perdido":
        return "bg-red-100 dark:bg-red-900/30 text-red-700 dark:text-red-300"
      default:
        return "bg-gray-100 dark:bg-gray-900/30 text-gray-700 dark:text-gray-300"
    }
  }

  return (
    <div className="p-4 md:p-6 space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-foreground">Gestión de Leads</h1>
        <p className="text-muted-foreground mt-1">
          Administra tu pipeline de ventas con seguimiento completo de leads turísticos
        </p>
      </div>

      <Card className="p-4 md:p-6">
        <div className="flex flex-col gap-4 mb-6">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-3">
            <div className="relative w-full sm:w-64">
              <Search className="absolute left-3 top-3 w-4 h-4 text-muted-foreground" />
              <input
                type="text"
                placeholder="Buscar por nombre, email, teléfono..."
                className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-card text-foreground placeholder-muted-foreground focus:outline-none focus:ring-2 focus:ring-orange-500"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            <div className="flex flex-wrap gap-2 w-full sm:w-auto">
              <Button
                onClick={() => setIsModalOpen(true)}
                className="bg-orange-600 hover:bg-orange-700 text-white gap-2 flex-1 sm:flex-none"
              >
                <Plus className="w-4 h-4" />
                Lead
              </Button>
              <Button
                onClick={() => setIsImportOpen(true)}
                className="bg-green-600 hover:bg-green-700 text-white gap-2 flex-1 sm:flex-none"
              >
                <Upload className="w-4 h-4" />
                Importar
              </Button>
              <Button
                onClick={() => setIsRulesOpen(true)}
                className="bg-indigo-600 hover:bg-indigo-700 text-white gap-2 flex-1 sm:flex-none"
              >
                <Settings className="w-4 h-4" />
                Reglas
              </Button>
            </div>
          </div>
        </div>

        <LeadsTable
          leads={filteredLeads}
          getEstadoColor={getEstadoColor}
          onDelete={handleDeleteLead}
          onUpdate={handleUpdateLead}
          onViewHistory={setSelectedLeadHistory}
        />
      </Card>

      <LeadModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSubmit={handleAddLead}
        paquetesDisponibles={paquetesDisponibles}
      />
      <ImportLeadsModal isOpen={isImportOpen} onClose={() => setIsImportOpen(false)} onSubmit={handleImportLeads} />
      <AssignmentRulesModal isOpen={isRulesOpen} onClose={() => setIsRulesOpen(false)} />
      {selectedLeadHistory && (
        <LeadHistory
          leadId={selectedLeadHistory}
          history={leadHistory[selectedLeadHistory] || []}
          onClose={() => setSelectedLeadHistory(null)}
        />
      )}
    </div>
  )
}
