"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { ClientesTable } from "./clientes-table"
import { ClienteModal } from "./cliente-modal"
import { ImportClientesModal } from "./import-clientes-modal"
import { ConvertLeadModal } from "./convert-lead-modal"
import { ClienteDetailModal } from "./cliente-detail-modal"
import { Plus, Search, Upload } from "lucide-react"

export function ClientesModule() {
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isImportOpen, setIsImportOpen] = useState(false)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [selectedClienteId, setSelectedClienteId] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState("")
  const [clientes, setClientes] = useState([
    {
      id: "1",
      nombre: "Hotel Las Playas",
      email: "contacto@lasplayas.com",
      ciudad: "Cancún",
      pais: "México",
      telefono: "+52 998 123 4567",
      whatsapp: "+52 998 123 4567",
      tipoCliente: "empresa",
      leads: 5,
      leadIds: ["1", "3"],
      fechaConversion: "2025-12-01",
      documentoId: "A123456789",
      tipoDocumento: "nif",
      destinosPreferidos: "Cancún, Riviera Maya",
      tipoViajes: ["Playa", "Relax"],
      presupuestoPromedio: "alto",
      frecuenciaViajes: "mensual",
      historial: [
        { fecha: "2025-12-01", evento: "Cliente creado" },
        { fecha: "2025-12-05", evento: "Lead convertido: Lead #1" },
      ],
    },
    {
      id: "2",
      nombre: "Resort Paradise",
      email: "info@resortparadise.com",
      ciudad: "Ibiza",
      pais: "España",
      telefono: "+34 971 123 456",
      whatsapp: "+34 971 123 456",
      tipoCliente: "empresa",
      leads: 8,
      leadIds: ["2"],
      fechaConversion: "2025-12-05",
      documentoId: "B987654321",
      tipoDocumento: "nif",
      destinosPreferidos: "Ibiza",
      tipoViajes: ["Hotel"],
      presupuestoPromedio: "medio",
      frecuenciaViajes: "anual",
      historial: [{ fecha: "2025-12-05", evento: "Cliente creado" }],
    },
  ])

  const [availableLeads, setAvailableLeads] = useState([
    {
      id: "1",
      nombre: "Juan",
      apellido: "Pérez",
      email: "juan@example.com",
      telefono: "+52 998 123 4567",
      tipoConsulta: "Paquete",
      origen: "Web",
      estado: "Nuevo",
    },
    {
      id: "2",
      nombre: "María",
      apellido: "García",
      email: "maria@example.com",
      telefono: "+34 612 345 678",
      tipoConsulta: "Hotel",
      origen: "Instagram",
      estado: "En gestión",
    },
  ])

  const handleAddCliente = (data: any) => {
    const newCliente = {
      id: String(clientes.length + 1),
      ...data,
      leads: 0,
      leadIds: [],
      fechaConversion: new Date().toISOString().split("T")[0],
      historial: [{ fecha: new Date().toISOString().split("T")[0], evento: "Cliente creado" }],
    }
    setClientes([...clientes, newCliente])
    setIsModalOpen(false)
  }

  const handleConvertLead = (leadId: string, clienteId: string) => {
    const lead = availableLeads.find((l) => l.id === leadId)
    if (!lead) return

    setClientes(
      clientes.map((c) =>
        c.id === clienteId
          ? {
              ...c,
              leads: c.leads + 1,
              leadIds: [...c.leadIds, leadId],
              historial: [
                ...c.historial,
                {
                  fecha: new Date().toISOString().split("T")[0],
                  evento: `Lead convertido: ${lead.nombre} ${lead.apellido}`,
                },
              ],
            }
          : c,
      ),
    )
    setAvailableLeads(availableLeads.filter((l) => l.id !== leadId))
    setIsConvertLeadOpen(false)
  }

  const handleImportClientes = (importedClientes: any[]) => {
    const newClientes = importedClientes.map((cliente, idx) => ({
      id: String(clientes.length + idx + 1),
      ...cliente,
      leads: 0,
      leadIds: [],
      fechaConversion: new Date().toISOString().split("T")[0],
      historial: [{ fecha: new Date().toISOString().split("T")[0], evento: "Cliente importado" }],
    }))
    setClientes([...clientes, ...newClientes])
    setIsImportOpen(false)
  }

  const handleDeleteCliente = (id: string) => {
    setClientes(clientes.filter((c) => c.id !== id))
  }

  const handleViewDetail = (id: string) => {
    setSelectedClienteId(id)
    setIsDetailOpen(true)
  }

  const filteredClientes = clientes.filter((c) => c.nombre.toLowerCase().includes(searchTerm.toLowerCase()))

  return (
    <div className="p-4 md:p-6 space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-foreground">Gestión de Clientes</h1>
        <p className="text-muted-foreground mt-1">
          Administra tu base de clientes con conversión de leads e historial completo
        </p>
      </div>

      <Card className="p-4 md:p-6">
        <div className="flex flex-col gap-4 mb-6">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-3">
            <div className="relative w-full sm:w-64">
              <Search className="absolute left-3 top-3 w-4 h-4 text-muted-foreground" />
              <input
                type="text"
                placeholder="Buscar cliente..."
                className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-card text-foreground placeholder-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            <div className="flex flex-wrap gap-2 w-full sm:w-auto">
              <Button
                onClick={() => setIsModalOpen(true)}
                className="bg-purple-600 hover:bg-purple-700 text-white gap-2 flex-1 sm:flex-none"
              >
                <Plus className="w-4 h-4" />
                Cliente
              </Button>
              <Button
                onClick={() => setIsImportOpen(true)}
                className="bg-green-600 hover:bg-green-700 text-white gap-2 flex-1 sm:flex-none"
              >
                <Upload className="w-4 h-4" />
                Importar
              </Button>
            </div>
          </div>
        </div>

        <ClientesTable clientes={filteredClientes} onDelete={handleDeleteCliente} onViewDetail={handleViewDetail} />
      </Card>

      <ClienteModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} onSubmit={handleAddCliente} />
      <ConvertLeadModal
        isOpen={isConvertLeadOpen}
        onClose={() => setIsConvertLeadOpen(false)}
        leads={availableLeads}
        clientes={clientes}
        onConvert={handleConvertLead}
      />
      <ImportClientesModal
        isOpen={isImportOpen}
        onClose={() => setIsImportOpen(false)}
        onSubmit={handleImportClientes}
      />
      {selectedClienteId && (
        <ClienteDetailModal
          isOpen={isDetailOpen}
          onClose={() => setIsDetailOpen(false)}
          cliente={clientes.find((c) => c.id === selectedClienteId)}
        />
      )}
    </div>
  )
}
